@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
/* Default CSS */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    scroll-behavior: smooth;
    scroll-padding-top: 75px;
    max-width: 100vw;
}

body {
    font-weight: 400;
    overflow-x: hidden;
    position: relative;
    background-color: #121212 !important;
    color: #fff !important;
    font-family: 'Poppins', sans-serif !important;
}

textarea {
    font-family: 'Poppins', sans-serif !important;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
    padding: 0;
    line-height: normal;
}

p,
a,
li,
button,
ul {
    margin: 0;
    padding: 0;
    line-height: normal;
    text-decoration: none;
}

a:hover {
    text-decoration: none;
}

img {
    width: 100%;
    height: auto;
}

input,
button {
    border: 0;
    background-color: transparent;
}

input:focus,
textarea:focus,
select:focus {
    outline: none;
}

/* ===== Scrollbar CSS ===== */
  /* Firefox */ * {
    scrollbar-width: thin;
    scrollbar-color: #aab #ffffff;
}

/* Chrome, Edge, and Safari */
*::-webkit-scrollbar {
    width: 7px;
}

*::-webkit-scrollbar-track {
    background: #aaaaaa00;
}

*::-webkit-scrollbar-thumb {
    background-color: #aab;
    border-radius: 6px;
    border: 1px solid #aaaaaa00;
}
body {
    background-image: url(/static/media/background.5f3ca497.png);
    background-repeat: no-repeat;
    background-attachment: fixed;
}
.header {
    position: fixed;
    top: 25px;
    left: 50%; 
    -webkit-transform: translateX(-50%); 
            transform: translateX(-50%);
    width: 95%;
    height: 60px;
    padding: 0 2%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background: rgba(255, 255, 255, 0.2);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    -webkit-backdrop-filter: blur(2px);
            backdrop-filter: blur(2px);
    border-radius: 8px;
    z-index: 1000;
}

.logo {
    margin: auto 0;
    width: 50px;
    height: 50px;
}

.lists {
    display: flex;
}

.list-links {
    height: 100%;
    list-style: none;
    display: flex;
    align-items: center;
}

.list-links li {
    margin-right: 60px;
}

.list-links li a {
    font-size: 0.98rem;
    font-weight: 500;
    color: #aab;
}

.list-social {
    height: 100%;
    display: flex;
    list-style: none;
    align-items: center;
}

.list-social li {
    margin: auto 20px;
}

.list-social li img {
    display: flex;
    align-items: center;
    height: 40px;
    padding: 5px;
    border: 1px solid #aab;
    fill: #aab;
    border-radius: 25px;
}
        
.list-links li:hover a, .list-links li:hover,.list-social li img:hover {
    color: #ccd;
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    transition: all 800ms;
}

.mobile {
    display: none;
}
@media (max-width:900px) { 
    .pc {
        display: none;
    }
    .mobile {
        display: flex;
        margin-right: 8px;
    }
    .header {
        position: relative;
        top: 0;
        left: 0;
        -webkit-transform: translateX(0);
                transform: translateX(0);
        width: 100%;
        border-radius: 0;
    }
    .logo {
        width: 35px;
        height: 35px;
        margin: auto 5px;
    }
    .list-links li {
        width: 20px;
        height: 20px;
        margin: auto 5px;
    }
    .list-social li {
        margin: auto 5px;
    }
    .list-social li img {
        width: 30px;
        height: 30px;
        padding: 5px;
        border-radius: 15px;
    }
}

