#home {
    display: flex;
    width: 90%;
    justify-content: space-between;
    margin: 150px auto;
}

.texts {
    width: 55%;
}

.tagline{
    background: #42275a;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #734b6d, #42275a); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    padding: 5px;
    color:  #ccc;
    border: 1px solid #ccc; 
    font-size: 0.8rem;
}

.home-title {
    font-size: 4rem;
    font-weight: 400;
}

.image {
    width: 35%;
    -webkit-animation: anim 2s ease-in-out infinite;
            animation: anim 2s ease-in-out infinite;
}

.txt-rotate > .wrap {
    padding-right: 3px;
    border-right: 0.08em solid #666;
}

@-webkit-keyframes anim {
    0% {
        -webkit-transform: translateY(0%);
                transform: translateY(0%);
    }
    50% {
        -webkit-transform: translateY(10%);
                transform: translateY(10%);
    }
    100% {
        -webkit-transform: translateY(0%);
                transform: translateY(0%);
    }
}

@keyframes anim {
    0% {
        -webkit-transform: translateY(0%);
                transform: translateY(0%);
    }
    50% {
        -webkit-transform: translateY(10%);
                transform: translateY(10%);
    }
    100% {
        -webkit-transform: translateY(0%);
                transform: translateY(0%);
    }
}
@media (max-width: 900px) { 
