.contact {
    display: flex;
    justify-content: space-between;
    background: #7f00ff; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #7f00ffaa, #e100ffaa); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.contact h2 {
	font-size: 45px;
	font-weight: 700;
  margin-bottom: 30px;
}

.container2 {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 90%;
    margin: 150px auto;
}

.img-container {
    width: 40%;
}

.form-container {
    width: 55%;
}

.form {
    width: 100%;
    display: flex;
    flex-direction: column;
    flex: 1 1;
}

.gridform {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.gridform input {
    width: 49%;
}

.form input,.form textarea {
    background: rgba(255, 255, 255, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.5);
    border-radius: 20px;
    color: #fff;
    margin: 0 0 8px 0;
    padding: 18px 26px;
    font-weight: 500;
    font-size: 18px;
    letter-spacing: 0.8px;
    transition: 0.3s ease-in-out;
}

.contact form input:focus, .contact form textarea:focus {
    background: rgba(255, 255, 255, 1);
    color: #121212;
}

.contact form input::-webkit-input-placeholder, .contact form textarea::-webkit-input-placeholder {
    font-size: 16px;
    font-weight: 400;
    color: #fff;
}

.contact form input::placeholder, .contact form textarea::placeholder {
    font-size: 16px;
    font-weight: 400;
    color: #fff;
}

.contact form input:focus::-webkit-input-placeholder, .contact form textarea:focus::-webkit-input-placeholder {
    color: #121212;
    opacity: 0.8;
}

.contact form input:focus::placeholder, .contact form textarea:focus::placeholder {
    color: #121212;
    opacity: 0.8;
}

.contact form button {
    width: 30%;
    font-weight: 700;
    color: #000;
    background-color: #fff;
    padding: 14px 48px;
    font-size: 18px;
    margin-top: 25px;
    border-radius: 0;
    position: relative;
    transition: 0.3s ease-in-out;
}

.contact form button span {
    z-index: 1;
    position: relative;
}

.contact form button:hover {
    color: #fff;
}

.contact form button::before {
    content: "";
    background: #121212;
    width: 0;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
    transition: 0.3s ease-in-out;
}

.contact form button:hover::before {
    width: 100%;
}
@media (max-width:900px) { 
